import * as React from 'react';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemText from '@material-ui/core/ListItemText';
import Chip from '@material-ui/core/Chip';
import ListItemAppLink from 'gatsby-theme-aoi/src/components/ListItemAppLink';
import { ArtistItem } from '../utils/graphql-hooks';

interface Props {
  items: ArtistItem[];
  title: string;
  itemTitle: (item: ArtistItem) => string;
  itemValue: (item: ArtistItem) => string | number;
}

function Rank({ items, title, itemTitle, itemValue }: Props) {
  return (
    <List subheader={<ListSubheader>{title}</ListSubheader>}>
      {items.map((item) => (
        <ListItemAppLink key={item.fieldValue} button to={`/artist/${item.fieldValue}/`}>
          <ListItemText primary={itemTitle(item)} />
          <Chip label={itemValue(item)} />
        </ListItemAppLink>
      ))}
    </List>
  );
}

export default Rank;
